export default {
	name: 'uni-drawer',
	props: {
		/**
		 * 显示状态
		 */
		visible: {
			type: Boolean,
			default: false
		},
		/**
		 * 显示模式（左、右），只在初始化生效
		 */
		mode: String,
		/**
		 * 蒙层显示状态
		 */
		mask: {
			type: [Boolean, String],
			default: true
		}
	},
	data() {
		return {
			visibleSync: false,
			showDrawer: false,
			rightMode: false,
			closeTimer:null,
			watchTimer:null
		}
	},
	watch: {
		visible(val) {
			clearTimeout(this.watchTimer)
			setTimeout(() => {
				this.showDrawer = val
			}, 100)
			if(this.visibleSync){
				clearTimeout(this.closeTimer)
			}
			if (val) {
				this.visibleSync = val
			} else {
				this.watchTimer = setTimeout(() => {
					this.visibleSync = val
				}, 300)
			}
		}
	},
	computed: {
		showMask() {
			return String(this.mask) === 'true'
		}
	},
	created() {
		this.visibleSync = this.visible
		setTimeout(() => {
			this.showDrawer = this.visible
		}, 100)
		this.rightMode = this.mode === 'right'
	},
	methods: {
		close() {
			this.showDrawer = false
			this.$emit('close')
			this.closeTimer = setTimeout(() => {
				this.visibleSync = false
			}, 200)
		},
		moveHandle(){}
	}
}
